import { defineComponent, computed, ref, watch } from 'vue';
import TaBle from '@/base-ui/table';
import { useStore } from '@/store';
import { userPermission } from '@/hooks/usePessimion';
export default defineComponent({
  props: {
    ContentToTable: {
      type: Object,
      require: true
    },
    pageName: {
      type: String,
      require: true
    },
    header: {
      type: String,
      default: '新建用户'
    }
  },
  emits: ['newBtnClick', 'EditBtnclick'],

  setup(props, {
    emit
  }) {
    const store = useStore(); //权限的检查

    const isCreate = userPermission(props.pageName ?? '', 'create');
    const isUpdate = userPermission(props.pageName ?? '', 'update');
    const isDelete = userPermission(props.pageName ?? '', 'delete');
    const isQuery = userPermission(props.pageName ?? '', 'query');
    const PageInfo = ref({
      currentPage: 1,
      pageSize: 10
    });
    const curPageInfo = ref();
    watch(PageInfo, () => {
      getPageData();
    }); //发送网络请求写flag的目的是为了刷新的时候，取第一页的数据

    const getPageData = (queryInfo = {}, flag = true) => {
      curPageInfo.value = {
        offset: flag ? (PageInfo.value.currentPage - 1) * PageInfo.value.pageSize : 0,
        size: PageInfo.value.pageSize,
        ...queryInfo
      };
      if (!isQuery) return;
      store.dispatch('system/getPages', {
        pageName: props.pageName,
        pageInfo: curPageInfo.value
      });
    };

    getPageData(); //获取system模块的数据

    const PageList = computed(() => store.getters['system/pageListData'](props.pageName));
    const PageCount = computed(() => store.getters['system/pageListCount'](props.pageName)); //获取动态的插槽

    const OtherPropsSlots = props.ContentToTable?.propList.filter(item => {
      if (item.prop == 'updateAt' || item.prop == 'createAt' || item.prop == 'handler') {
        return false;
      }

      return true;
    }); //删除/修改/新建用户

    const DeleteInfo = Info => {
      store.dispatch('system/deletePage', {
        pageName: props.pageName,
        pageInfo: {
          id: Info.id,
          curPageInfo: curPageInfo.value
        }
      });
    };

    const handleNewClick = () => {
      emit('newBtnClick');
    };

    const handleEditClick = item => {
      emit('EditBtnclick', item);
    };

    return {
      PageList,
      getPageData,
      PageCount,
      PageInfo,
      OtherPropsSlots,
      isCreate,
      isUpdate,
      isDelete,
      DeleteInfo,
      handleNewClick,
      handleEditClick
    };
  },

  components: {
    TaBle
  }
});