import { defineComponent } from 'vue';
export default defineComponent({
  name: 'TaBle',

  setup(props, {
    emit
  }) {
    //这个方法也可以修改size和当前页面的偏移量

    /* const pageSize4 = ref(props.Page.pageSize)
    watch(pageSize4, (newValue) => {
      console.log(666)
      emit('update:Page', { ...props.Page, ['pageSize']: newValue })
    }) */
    const handleCurrentChange = currentPage => {
      emit('update:Page', { ...props.Page,
        currentPage
      });
    };

    const handleSizeChange = pageSize => {
      emit('update:Page', { ...props.Page,
        pageSize
      });
    };

    return {
      handleCurrentChange,
      handleSizeChange
    };
  },

  props: {
    ListDate: {
      type: Array,
      required: true
    },
    childrenProps: {
      type: Object,
      default: () => {
        return {};
      }
    },
    Page: {
      type: Object,
      default: () => {
        return {
          currentPage: 1,
          pageSize: 10
        };
      }
    },
    ListCount: {
      type: Number,
      default: 0
    },
    propList: {
      type: Array,
      required: true
    },
    showIndex: {
      type: Boolean,
      default: false
    },
    showSelect: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    showFooter: {
      type: Boolean,
      default: true
    }
  }
});